 .dark-profile-body{
    background-color: #090b28;
 }

  .dark-navbar{ 
    padding: 1rem;
    background-color: #2c135f;
  }

  
  a {
    color: #333;
    text-decoration: none;
  }


  .dark-main-header h1 span {
    color: #ff1022;
    font-size: 64px;
  }
  
  .dark-card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    background: #2c135f;
    margin-bottom: 2rem;
    border-radius: 1rem;
    color: white;
  }
  
  .dark-card h2{
    margin-bottom: 1rem;
    font-size: 32px;
  }
  
  
  .dark-card > div {
    padding: 2rem;
  }
  
  .dark-card:nth-child(even) img {
    order: 2;
    order: 2;
    border-bottom-left-radius: 0rem;
    border-top-left-radius: 0rem;
    border-bottom-right-radius: 1rem;
    border-top-right-radius: 1rem;
  }
  
  
  .dark-btn {
    display: inline-block;
    border: 2px solid #ff1022;
    background: #2c135f;
    color: #ff1022;
    padding: 0.8rem 1.8rem;
    margin-top: 2rem;
    cursor: pointer;
    margin-right: 1rem;
  }


  
  @media(max-width:600px) {
    .dark-card {
      display: block;
      border-bottom-left-radius: 0rem;
      border-bottom-right-radius: 0rem;
      border-top-right-radius: 1rem;
      border-top-left-radius: 1rem;
    }
    .dark-card img{
        border-bottom-left-radius: 0rem;
        border-top-left-radius: 1rem;
        border-bottom-right-radius: 0rem;
        border-top-right-radius: 1rem;
      }
  
  }
